<template>
  <div class="container content">
    <div class="row">
      <div class="col-12">
        <h1 class="text-primary">Transaksi Paket Data</h1>
      </div>
    </div>
    <div class="row mt-3">
      <div class="col-12 mb-2" v-show="this.dataCheckNoTujuan.valid">
        <div
          class="card bg-putih shadow rounded-10"
          id="result-card"
          style="display: block"
        >
          <div class="card-body">
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Indosat Ooredoo'"
              src="@/assets/operator/indosat-ooredoo.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'XL Axiata'"
              src="@/assets/operator/xl-axiata.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == '3'"
              src="@/assets/operator/3.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Smartfren'"
              src="@/assets/operator/smartfren.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Telkomsel'"
              src="@/assets/operator/telkomsel.jpg"
              id="logo-operator"
            />
          </div>
        </div>
      </div>
      <div
        v-show="
          this.dataCheckNoTujuan.valid == false && this.dataCheckNoTujuan != ''
        "
        class="col-12 mt-2"
      >
        <div class="alert alert-danger rounded-10" role="alert">
          Masukkan no tujuan yang valid!
        </div>
      </div>
      <div class="col-12">
        <div class="card bg-putih shadow rounded-10">
          <div class="card-body">
            <form class="text-left">
              <div class="form-group">
                <label for="no_tujuan" class="text-primary">No HP</label>
                <input
                  @input="checkProvider"
                  v-model="this.noTujuan"
                  id="no_tujuan"
                  required=""
                  autocomplete="off"
                  type="text"
                  class="
                    form-control form-control-lg
                    border border-primary
                    rounded-10
                  "
                  name="no_hp"
                  pattern="\d*"
                  placeholder="Masukkan Nomor Tujuan.."
                />
              </div>
              <!-- <div>
                <router-link
                  to="/customer/transaksi/piu2"
                  type="submit"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Lanjut
                </router-link>
              </div> -->
              <div class="form-group">
                <label for="produk">Jenis Paket Data </label>
                <select
                  v-model="idOperator"
                  id="jenis_produk"
                  name="jenis_produk"
                  required
                  class="form-control form-control-lg rounded-10"
                  @change="loadProduct()"
                >
                  <option value="">Pilih</option>
                  <template v-for="jp in jenisProduct" :key="jp">
                    <option
                      :value="jp.product_id"
                      v-if="this.PilihanIdProduct.indexOf(jp.product_id) > -1"
                    >
                      {{ jp.product_name }}
                    </option>
                  </template>
                </select>
              </div>
              <div class="form-group">
                <label for="produk"
                  >Produk
                  <span
                    v-show="this.user.verified == '0'"
                    class="badge badge-danger"
                    >Saldo : Rp. {{ numberFormat(this.user.saldo) }}</span
                  ></label
                >
                <select
                  v-model="Product"
                  id="produk"
                  name="produk"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option value="">Pilih</option>
                  <option
                    :value="product"
                    v-for="product in orderedProduct"
                    :key="product"
                  >
                    {{ product.operator_name }}
                    {{ product.product_name }} Harga: Rp.
                    {{ numberFormat(product.product_price + 5000) }}
                  </option>
                </select>
              </div>
              <div v-if="this.Product" class="card rounded-10">
                <div class="card-header text-center">Pembelian</div>
                <div class="card-body">
                  <div class="row">
                    <div class="col-6">Jenis Paket</div>
                    <div class="col-6">{{ this.Product.operator_name }}</div>
                  </div>
                  <div class="row">
                    <div class="col-6">ID Produk</div>
                    <div class="col-6">{{ this.Product.product_id }}</div>
                  </div>
                  <div class="row">
                    <div class="col-6">Produk</div>
                    <div class="col-6">{{ this.Product.product_name }}</div>
                  </div>
                  <div class="row">
                    <div class="col-6">Produk Detail</div>
                    <div class="col-6">{{ this.Product.product_detail }}</div>
                  </div>
                  <div v-if="this.Product.product_syarat" class="row">
                    <div class="col-6">Syarat</div>
                    <div class="col-6">{{ this.Product.product_syarat }}</div>
                  </div>
                  <div class="row">
                    <div class="col-6">Harga</div>
                    <div class="col-6">
                      {{ this.Product.product_price + 5000 }}
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="this.user.verified == '1'" class="form-group">
                <label for="metode_pembayaran">Metode Pembayaran</label>
                <select
                  v-model="metodePembayaran"
                  id="metode_pembayaran"
                  name="metode_pembayaran"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option value="">Pilih</option>
                  <option :value="'Hutang'">Bayar Nanti</option>
                  <option :value="'Dompet'">
                    Dompet | Saldo Anda: Rp. {{ numberFormat(this.user.saldo) }}
                  </option>
                </select>
              </div>
              <div>
                <button
                  @click="kirim()"
                  type="button"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Kirim
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOperator } from "no-telp";
import Swal from "sweetalert2";
import axios from "axios";
import _ from "lodash";

export default {
  name: "CusTransaksiPDView",
  props: ["ipAdress"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      noTujuan: null,
      dataCheckNoTujuan: {},
      PilihanIdProduct: null,
      idOperator: null,
      Product: null,
      metodePembayaran: null,
      jenisProduct: null,
      dataLoadProduct: null,
    };
  },
  methods: {
    kirim() {
      if (this.user.verified == "0") {
        this.metodePembayaran = "Dompet";
      }
      if (!this.noTujuan) {
        Swal.fire({
          icon: "error",
          title: "No HP Kosong",
          text: "silahkan masukkan no hp yang benar!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.Product) {
        Swal.fire({
          icon: "error",
          title: "Produk Kosong",
          text: "silahkan pilih produk!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.metodePembayaran) {
        Swal.fire({
          icon: "error",
          title: "Metode Pembayaran Kosong",
          text: "silahkan pilih metode pembayaran!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        Swal.fire({
          title: "Yakin untuk mengirim?",
          text: "Pastikan data form sudah benar!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yakin",
        }).then((result) => {
          if (result.isConfirmed) {
            const formData = new FormData();
            formData.append("metode_pembayaran", this.metodePembayaran);
            formData.append("no_hp", this.noTujuan);
            formData.append("produk", this.Product.product_id);
            formData.append("operatorProduk", this.idOperator);
            formData.append("cus_id", this.user.id);

            axios({
              method: "post",
              url:
                this.ipAdress +
                "api/transaksi/pd?username=" +
                this.user.username,
              data: formData,
              headers: { "Content-Type": "multipart/form-data" },
            })
              .then(function (response) {
                //handle success
                // console.log(response);
                if (response.data.status == "failed") {
                  Swal.fire({
                    icon: "error",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else if (response.data == "") {
                  Swal.fire({
                    icon: "success",
                    title: "Diproses",
                    text: "Transaksi Diproses",
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else {
                  Swal.fire({
                    icon: "success",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                }
              })
              .catch(function (response) {
                //handle error
                console.log(response);
              });
          }
        });
      }
    },
    loadProduct() {
      this.Product = null;
      axios
        .get(
          this.ipAdress +
            "api/get-prabayar-produk/" +
            this.idOperator +
            "?username=" +
            this.user.username
        )
        .then(({ data }) => {
          this.dataLoadProduct = data;
          // console.log(data);
        });
    },

    loadJenisProduct() {
      axios
        .get(
          this.ipAdress +
            "api/get-prabayar-operator/PD" +
            "?username=" +
            this.user.username
        )
        .then(({ data }) => {
          this.jenisProduct = data;
          // this.jenisProduct = data.filter(function (el) {
          //   return this.PilihanIdProduct.indexOf(el.product_id) > -1;
          // });
          // console.log(data);
        });
    },

    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },

    checkProvider: function () {
      this.dataCheckNoTujuan = getOperator(this.noTujuan);
      if (this.dataCheckNoTujuan.operator == "Indosat Ooredoo") {
        this.PilihanIdProduct = [
          "PDISTY",
          "PDISTB",
          "PDISTFH",
          "PDISTF",
          "PDISTUNL",
          "PDISTFCA",
          "PDISTFCNA",
        ];
        this.loadJenisProduct();
      } else if (this.dataCheckNoTujuan.operator == "XL Axiata") {
        this.PilihanIdProduct = [
          "PDXLBJ",
          "PDXLXCF",
          "PDXLH",
          "PDXLB",
          "PDXLCL",
          "PDXLXC",
          "PDXLXCVP",
          "PDXLGH",
          "PDXLXON",
        ];
        this.loadJenisProduct();
      } else if (this.dataCheckNoTujuan.operator == "3") {
        this.PilihanIdProduct = [
          "PDTRB",
          "PDTRAON",
          "PDTRHP",
          "PDTRMN",
          "PDTRGM",
          "PDTRUNL",
          "PDTRSP",
          "PDTRV",
          "PDTRIBD",
        ];
        this.loadJenisProduct();
      } else if (this.dataCheckNoTujuan.operator == "Smartfren") {
        this.PilihanIdProduct = ["PDSFUNL", "PDSFNS", "PDSFVB", "PDSFVNS"];
        this.loadJenisProduct();
      } else if (this.dataCheckNoTujuan.operator == "Telkomsel") {
        this.PilihanIdProduct = [
          "PDTSLF",
          "PDTSLGH",
          "PDTSLBGD",
          "PDTSLKF",
          "PDTSLKW",
          "PDTSLKY",
          "PDTSLKI",
          "PDTSLKT",
          "PDTSLHA",
          "PDTSLHB",
          "PDTSLHC",
          "PDTSLHD",
          "PDTSLM",
        ];
        this.loadJenisProduct();
      }
    },
  },
  computed: {
    orderedProduct: function () {
      return _.orderBy(this.dataLoadProduct, "product_price", "asc");
    },
  },
  mounted() {},
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}
#logo-operator {
  width: 30%;
}
</style>